<template>
  <header class="clearfix" :class="{'English': languageData.id == 'English'}" :updateStatusData="updateStatusData">
    <div class="left" v-show="navBoth.logo">
        <div class=" " style="height: 50px;padding: 0px 10px;" v-on:click="goIndex()">
          <img :src="loginImg" >
        </div>
    </div>
    <div class="left" v-show="navBoth.logo">
      <p class="logoText EnglishLogoText" v-on:click="goIndex()">{{languageData.header.logoText}}</p>
    </div>
    <div class="nav left clearfix" style="margin-left: 40px;" v-show="navBoth.nav" :class="check">
      <div class="navBox" v-on:click="gotChart(1)"><p >{{languageData.header.transport}}</p></div>
      <div class="navBox" v-on:click="gotChart(2)"><p >{{languageData.header.histroy}}</p></div>
      <div class="navBox" v-on:click="gotAdd()"><p class="el-icon-document" style="color:#136eb4"> {{languageData.header.creatOder}}</p></div>
      <div class="cleae"> </div>
      <div class="red"></div>
    </div>
    <div class="right clearfix">
      <div class="right" v-show="navBoth.user">
        <div class="imgBox">
          <img :src="user.headImg?user.headImg:null"/>
        </div>
      </div>
      <div class="right" v-show="navBoth.user">
        <el-popover ref="popover3" v-model="show2" placement="bottom" width="80" trigger="click">
          <div class="list" v-on:click="loginOut(user.id)">{{languageData.header.Exit}}</div>
        </el-popover>
        <p v-popover:popover3><a  style="cursor: pointer">{{user.nickName?user.nickName:''}} <i class="el-icon-arrow-down "> </i></a></p>
      </div>
      <div class="right" v-show="navBoth.language ">
        <div class="icon">{{navBoth.us?'':'|' }}</div>
      </div>
      <div class="right clearfix" v-show="navBoth.language">
        <div class="left" style="position: relative">
          <el-popover ref="popover4" v-model="show" placement="bottom" width="80" trigger="click">
            <div class="list" v-on:click="languageCheng('Chinese')">{{languageData.header.potin}}</div>
            <div class="list" v-on:click="languageCheng('English')">{{languageData.header.potin2}}</div>
          </el-popover>
          <p v-popover:popover4><a  style="cursor: pointer">{{languageData.header.language}} <i class="el-icon-arrow-down"></i></a></p>
        </div>
      </div>
      <div class="right" v-show="navBoth.us">
       <div class=" icon">{{navBoth.us==true || navBoth.language==true ?'|':'' }}</div>
      </div>
      <div class="right clearfix" v-show="navBoth.us">
        <div class="left">
          <div class="iocnImg iconService">
          </div>
        </div>
        <div class="left" >
          <p ><a  style="cursor: pointer">{{languageData.header.Contact}} </a></p>
        </div>
      </div>
      <div class="right" v-show="navBoth.login">
        <div class="icon">|</div>
      </div>
      <div class="right" v-show="navBoth.user">
        <div class="icon">|</div>
      </div>
      <div class="right clearfix" v-show="navBoth.login">
        <div class="left">
          <div class="iocnImg iconLogin">
          </div>
        </div>
        <div class="left" >
          <p><a  style="cursor: pointer" v-on:click="goLogin()">{{languageData.header.Login}}</a></p>
        </div>
      </div>
      <div class="right clearfix" v-show="navBoth.user">
        <div class="left">
          <div class="iocnImg iconLogin">
          </div>
        </div>
        <div class="left" >
          <p><a  style="cursor: pointer" v-on:click="goAdmin()">{{languageData.header.admin}}</a></p>
        </div>
      </div>
      <div class="right" >
        <div class="icon">|</div>
      </div>
      <div class="right clearfix" >
        <div class="left">
          <div class="iocnImg el-icon-download" style="background-color: white">
          </div>
        </div>
        <div class="left" style="position: relative">
          <el-popover ref="popover5" v-model="show5" placement="bottom" width="250" trigger="hover" >
            <div class="left" style="margin-left: 10px;margin-top: 10px">
              <div class="code Ios" >
                <img :src="ios"/>
              </div>
              <p style="line-height: 30px;text-align: center;">ios  </p>
            </div>
            <div class="left" style="margin-left: 20px; margin-top: 10px">
              <div class="code Android" >
                <img :src="android"/>
              </div>
              <p style="line-height: 30px;text-align: center"> android </p>
            </div>
            <div class="cleae">

            </div>
          </el-popover>
          <p v-popover:popover5
          ><a  style="cursor: pointer" >{{languageData.header.download}}</a></p>
        </div>
      </div>
    </div>
  </header>
</template>
<style scoped>
  /*基础 中文样式*/
  header {width: 100%; height: 50px;background-color: white }
  header p {line-height: 50px;font-size: 14px;text-align: left;color: #262626}
  header p a{line-height: 50px;font-size: 14px;text-align: left;color: #262626}
  .imgBox {width: 30px;height: 30px;background:#e1e1e1;margin: 10px;margin-right:40px;}
  .icon{width:45px;height: 10px;font-size: 12px;line-height:52px;text-align: center;color: #e1e1e1}
  .iocnImg{width: 16px;height: 16px;background: #e1e1e1;margin-top: 16px;margin-right: 5px;}
  .logoText{color: #136eb4;font-size:28px;line-height: 50px;cursor: pointer}
  .logo{margin-left: 15px;font-size: 36px;font-weight: bold;margin-right: 5px;}
  .iconService{background: url("../../../static/img/icon/iconService.png")}
  .iconLogin{background: url("../../../static/img/icon/iconLogin.png")}
  .navBox {width:120px;height: 48px; float: left;text-align: center;cursor: pointer ;border-bottom: 2px solid #fff;}
  .navBox p{text-align: center;font-size: 16px;}
  .red {width: 80px;  border-bottom: 2px solid #136eb4;position: relative;left: 20px;display: none}
  .list { cursor: pointer ; text-align: center }
  .chart   .red {left: 20px;display: block}
  .Histroy   .red {left: 140px;display: block}
  .creatOder   .red {left: 250px;width: 102px;display: block}
  /**英文样式覆盖**/
  .English .EnglishLogoText{ font-size: 14px;text-indent: 10px}
  .English  .navBox{ width: auto; margin-left: 30px;}
  .English  .red{ left: 30px;width: 120px}
  .English .transport .red{ left: 30px;width: 98px}
  .English .histroy .red{ left: 158px;width: 80px}
  .English .create .red{ left: 267px;width: 212px}
  .English .chart   .red {left: 20px;display: block}
  .English .Histroy   .red {left: 140px;display: block}
  .English .creatOder   .red {left: 250px;width: 250px;display: block}
  .code {width: 100px; height: 100px;border: 1px solid #e1e1e1;}
  .code img {max-width:100%}
</style>
<script>
  import { mapState, mapActions } from 'vuex'
  import Public from '../../vuex/public.js'
  import loginImg from '../../../static/img/icon/logo.png'
  import android from '../../../static/img/android.jpg'
  import ios from '../../../static/img/ios.jpg'
  export default {
    name: 'my-header',
    created () {
     // 查看本页面路由 绑定对应页面class
      this.check = this.$route.name
      var usr = sessionStorage.getItem('user')
      usr = JSON.parse(usr)
      if(usr) {
        if(usr.type == 0){
          this.$router.push({ name: 'AdminConfig' })
        }
      }else {
        if(this.check != 'login'){
             this.$router.push({ name: 'index' })
        }
      }
    },
    data () {
      return {
        platform: null,
        check: null,
        show: false,
        show2: false,
        show5: false,
        user: {
          nickName: '',
          img: '',
          id: null,
          type: null
        },
        android: android,
        ios: ios,
        loginImg: loginImg,
        input:null
      }
    },
    methods: {
      ...mapActions({
        lognOutPost: 'loginOutFn'
      }),
      // 渲染地图
      mapFn (map) {
        map.setCenter({lat: 52.5160, lng: 13.3779})
        map.setZoom(13)
        map.setBaseLayer(this.defaultLayers.normal.traffic)
      },
      // 登陆
      goLogin () {
        this.$router.push({ name: 'login' })
      },
      goIndex () {
        this.$router.push({ name: 'map' })
      },
      gotAdd () {
        this.$router.push({ name: 'creatOder' })
      },
      gotChart (id) {
        if (id == 1) {
          this.$router.push({name: 'chart'})
        }
        if (id == 2) {
          this.$router.push({name: 'Histroy'})
        }
      },
      //  语言切换
      languageCheng (parm) {
        this.show = !this.show
        if (parm) {
          Public.publicLo.sessionStorageSav('Language',parm)
          console.log(Public.publicLo.sessionStorageGet('Language'))
          this.$store.commit('updateLanguage', parm)
        }
      },
      //
        goAdmin(){
          var test = window.location.href
          console.log(window.location.href)
          this.$router.push({ name: 'adminDavise' })
          //window.open('/adminDavise','_self')
      },
      // 退出
      loginOut (id) {
        this.show2 = !this.show2
        var parm = {
          userId: id
        }
        this.lognOutPost(parm)
      },
      openSuccess (text) {
        this.$message({
          message: text,
          type: 'success'
        })
      },
      openError (text) {
        this.$message.error(text)
      }
    },
    computed: {
      navBoth: function () {
        var parm = this.nav
        var userData = Public.publicLo.sessionStorageGet('user')
        this.user = {
          nickName: '',
          headImg: '',
          id: null
        }
        if (userData) {
          // 转换对象
          userData = JSON.parse(userData)
          // 渲染
          this.user = {
            nickName: userData.userName || '',
            headImg: userData.headImg || '',
            id: userData.userId
          }
          // 配置顶部菜单
          this.nav.user = true
          this.nav.login = false
          this.nav.nav = true
          this.nav.logo = true
          this.nav.language = true
        }
        return parm
      },
      languageData: function () {
        var parm = this.language
        if (parm) {

            if (parm.check == 'Chinese') {
              parm = parm.Chinese
            }
            if (parm.check == 'English') {
              parm = parm.English
//            dom.style = 'width:620px'
            }
//
//          // 替换 其他交互数据
//          this.updateDate(parm)
        }
        return parm
      },
      updateStatusData: function () {
        var parm = this.updateStatus
        if (parm == 'out') {
          // 没记住密码保存空 localStorage
          Public.publicLo.sessionStorageSav('token', '')
          Public.publicLo.sessionStorageSav('user', '')
          // 清空所有数据
          this.$store.commit('loginOut', null)
          this.$router.push({ name: 'login' })
          // this.lognOutPost(this.user)
        } else if (parm == 'outErorr') {
          Public.publicLo.sessionStorageSav('token', '')
          Public.publicLo.sessionStorageSav('user', '')
          // 清空所有数据
          this.$store.commit('loginOut', null)
          this.$router.push({ name: 'login' })
        }
        return 1
      },
      ...mapState([
        'nav',
        'language',
        'updateStatus'
      ])
    },
    mounted () {

    }
  }
</script>




